import styled from 'styled-px2vw';
import iconArrow from './images/icon-arrow.png';
import pkg from '../../../package.json';

const themeColor = pkg.theme['brand-primary'];

export const Content = styled.div`
  padding: 40px 30px;
  .am-accordion::before,
  .am-accordion .am-accordion-item .am-accordion-header::after,
  .am-accordion
    .am-accordion-item
    .am-accordion-content
    .am-accordion-content-box::after {
    display: none !important;
  }
  .am-accordion .am-accordion-item .am-accordion-header i {
    background-image: url(${iconArrow});
  }
  .am-accordion .am-accordion-item .am-accordion-header {
    border: 1px solid #e8e8e8 !important;
    border-radius: 16px;
  }
`;

export const TabBarBox = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-around;
`;

export const TabBarItem = styled.div`
  width: 190px;
  height: 50px;
  font-size: 32px;
  text-align: center;
  line-height: 50px;
  color: #7f7f7f;
  &.active {
    color: #fff;
    border-radius: 25px;
    font-weight: bold;
    background-color: ${themeColor};
  }
`;

export const AccordionBox = styled.div`
  padding: 40px;
`;

export const AccordionItem = styled.div`
  height: 150px;
  border-radius: 16px;
  border: 1px solid #e8e8e8;
  margin-top: 12px;
  display: flex;
  align-items: center;
  &:first-child {
    margin-top: 0;
  }
`;

export const AccordionLeft = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`;
export const AccordionRight = styled.div``;
