import React, { useState } from 'react';
import { Accordion, Picker, WhiteSpace } from 'antd-mobile';
import PickItem from '@/components/PickItem';
import { ejuSubject } from '@/constants/globalDatas';
import {
  AccordionBox,
  AccordionItem,
  AccordionLeft,
  AccordionRight,
  Content,
  TabBarBox,
  TabBarItem,
} from './styles';

const Library: React.FC = () => {
  const [tabType, setTabType] = useState('0'); // 标签类型
  const [ejuSubjectValue, setEjuSubjectValue] = useState<string[]>([]); // 科目

  // 切换标签
  const handleTabChange = (type: string): void => {
    setTabType(type);
  };
  return (
    <Content>
      <TabBarBox>
        <TabBarItem
          className={tabType === '0' ? 'active' : ''}
          onClick={(): void => handleTabChange('0')}
        >
          智能速答
        </TabBarItem>
        <TabBarItem
          className={tabType === '1' ? 'active' : ''}
          onClick={(): void => handleTabChange('1')}
        >
          专题模拟
        </TabBarItem>
        <TabBarItem
          className={tabType === '2' ? 'active' : ''}
          onClick={(): void => handleTabChange('2')}
        >
          套卷模拟
        </TabBarItem>
      </TabBarBox>
      <WhiteSpace size="xl" />

      <Picker
        data={ejuSubject}
        cols={1}
        value={ejuSubjectValue}
        onOk={(value: string[]): void => setEjuSubjectValue(value)}
      >
        <PickItem placeholder="请选择考学科目" />
      </Picker>

      <Accordion defaultActiveKey="0">
        <Accordion.Panel header="1 微积分">
          <AccordionBox>
            <AccordionItem>
              <AccordionLeft>111</AccordionLeft>
              <AccordionRight>222</AccordionRight>
            </AccordionItem>
            <AccordionItem>222</AccordionItem>
            <AccordionItem>333</AccordionItem>
          </AccordionBox>
        </Accordion.Panel>
      </Accordion>
    </Content>
  );
};

export default Library;
